import React from "react"
import Parser from "../components/parser"
import Layout from "../components/layout"
import Head from "../components/head"
import SEO from "../components/seo"

const Post = ({ pageContext }) => {
  const post = pageContext.article;
  const featuredImg = post.featuredImage?.url;

  const content = post.content || post.excerpt

  return (
    <Layout>
      <Head pageTitle={post.title} />
      <SEO title={post.title} />

      <div className={'post'}>
        <h1>
          <Parser data={post.title} />
        </h1>
        <div className={'contentPost'}>
          <div className={'imageContainerPost'}>
            {featuredImg && <img src={featuredImg} alt={post.slug} />}
          </div>
          <div className={'articlePost'}>
            <Parser data={content} />
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Post
